* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container-form {
  width: 100vw;
  height: 60px;
  background-color: #3457b4;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  box-shadow: 2px 2px 4px 1px #42445a;
}

input {
  width: 250px;
  height: 32px;
  background-color: #fff;
  border: none;
  padding-left: 10px;
}

input:focus, input:hover {
  outline: none;
}

.btn {
  height: 32px;
  width: 32px;
  background-color: #f1eff1;
  border: none;
  border-radius: 0 5px 5px 0;
  position: absolute;
  transform: translateX(-100%);
}

.icon {
  fill: #888;
  width: 30px;
  height: 20px;
  cursor: pointer;
  background-color: #f1eff1;
  border: none;
  margin-left: 1px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  padding: 65px 15px;
  display: flex;
}

.photo-card {
  width: 330px;
  height: 320px;
  padding: 1px solid white;
  box-shadow: 2px 2px 4px -1px #42445a;
}

.photo-card img {
  width: 330px;
  height: 250px;
  object-fit: cover;
  object-position: top;
  display: block;
}

.info {
  color: #161616;
  text-align: center;
  align-content: center;
  justify-content: space-between;
  padding: 15px 10px 5px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  gap: 5px;
  display: flex;
}

a {
  text-decoration: none;
}

.load-more {
  text-align: center;
  margin: 0 auto;
  display: block;
}

.hidden {
  display: none;
}

